"use client"; // Mark this component as a Client Component
import React, { useState } from 'react';
import Navbar from './Navbar'; // Adjust the import path as needed
import Sidebar from './Sidebar'; // Adjust the import path as needed
import Category from './category'; // Adjust the import path as needed
import Order from './rder'; // Import the Order component
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog'; // Adjust the import path as needed
import { Checkbox } from '@/components/ui/checkbox'; // Adjust the import path as needed

// MenuCard Component
const MenuCard: React.FC<{ name: string; description: string; price: number; imageUrl: string; onClick: () => void }> = ({
  name,
  description,
  price,
  imageUrl,
  onClick,
}) => {
  const cardStyle: React.CSSProperties = {
    borderRadius: '12px',
    backgroundColor: '#ffffff',
    boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
    overflow: 'hidden',
    transition: 'transform 0.2s, box-shadow 0.2s',
    cursor: 'pointer',
  };

  const imageStyle: React.CSSProperties = {
    width: '100%',
    height: '160px',
    objectFit: 'cover',
  };

  const contentStyle: React.CSSProperties = {
    padding: '16px',
  };

  const titleStyle: React.CSSProperties = {
    fontSize: '18px',
    fontWeight: 'bold',
    marginBottom: '8px',
  };

  const descriptionStyle: React.CSSProperties = {
    fontSize: '14px',
    color: '#666666',
    marginBottom: '12px',
  };

  const priceStyle: React.CSSProperties = {
    fontSize: '16px',
    fontWeight: 'bold',
    color: '#FFB800',
  };

  return (
    <div
      style={cardStyle}
      onMouseEnter={(e) => {
        e.currentTarget.style.transform = 'translateY(-4px)';
        e.currentTarget.style.boxShadow = '0 6px 12px rgba(0, 0, 0, 0.15)';
      }}
      onMouseLeave={(e) => {
        e.currentTarget.style.transform = 'none';
        e.currentTarget.style.boxShadow = '0 4px 6px rgba(0, 0, 0, 0.1)';
      }}
      onClick={onClick}
    >
      <img src={imageUrl} alt={name} style={imageStyle} />
      <div style={contentStyle}>
        <h3 style={titleStyle}>{name}</h3>
        <p style={descriptionStyle}>{description}</p>
        <p style={priceStyle}>${price.toFixed(2)}</p>
      </div>
    </div>
  );
};

// Restaurant Component
const Restaurant: React.FC = () => {
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [selectedIngredients, setSelectedIngredients] = useState<Set<string>>(new Set());

  const handleProductSelect = (product: Product) => {
    setSelectedProduct(product);
    // Initialize with default ingredients
    setSelectedIngredients(new Set(
      product.ingredients
        .filter(ing => ing.default)
        .map(ing => ing.name)
    ));
  };

  const handleIngredientToggle = (ingredientName: string, isDefault: boolean) => {
    const newSelected = new Set(selectedIngredients);
    if (newSelected.has(ingredientName)) {
      if (!isDefault) {
        // Only allow removing non-default ingredients
        newSelected.delete(ingredientName);
      }
    } else {
      newSelected.add(ingredientName);
    }
    setSelectedIngredients(newSelected);
  };

  const calculateTotalPrice = (product: Product, selectedIngs: Set<string>) => {
    const ingredientsPrice = product.ingredients
      .filter(ing => selectedIngs.has(ing.name))
      .reduce((sum, ing) => sum + ing.price, 0);
    return product.basePrice + ingredientsPrice;
  };

  const restaurantStyle: React.CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    gap: '24px',
    padding: '24px',
  };

  const mainContentStyle: React.CSSProperties = {
    display: 'flex',
    gap: '24px',
  };

  const menuGridStyle: React.CSSProperties = {
    display: 'grid',
    gridTemplateColumns: 'repeat(auto-fill, minmax(250px, 1fr))',
    gap: '24px',
    flex: 1,
  };

  const orderContainerStyle: React.CSSProperties = {
    width: '300px', // Fixed width for the Order component
    backgroundColor: '#ffffff',
    borderRadius: '12px',
    padding: '16px',
    boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
  };

  return (
    <div style={restaurantStyle}>
      <Navbar />
      <div style={mainContentStyle}>
        <Sidebar />
        <div style={{ flex: 1 }}>
          <Category />
          <div style={menuGridStyle}>
            {products.map((product) => (
              <MenuCard
                key={product.id}
                name={product.name}
                description={product.description}
                price={product.basePrice}
                imageUrl={product.image}
                onClick={() => handleProductSelect(product)}
              />
            ))}
          </div>
        </div>
        <div style={orderContainerStyle}>
          <Order />
        </div>
      </div>

      {/* Popup Dialog */}
      <Dialog open={!!selectedProduct} onOpenChange={() => setSelectedProduct(null)}>
        {selectedProduct && (
          <DialogContent style={{ maxWidth: '800px' }}>
            <DialogHeader>
              <DialogTitle style={{ fontSize: '24px', fontWeight: 'bold' }}>
                {selectedProduct.name}
              </DialogTitle>
            </DialogHeader>
            <div style={{ marginTop: '16px' }}>
              <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '24px' }}>
                <div>
                  <img
                    src={selectedProduct.image}
                    alt={selectedProduct.name}
                    style={{ borderRadius: '12px', objectFit: 'cover', width: '100%', height: '300px' }}
                  />
                  <div style={{ marginTop: '16px' }}>
                    <p style={{ fontSize: '14px', color: '#666666' }}>
                      <span style={{ fontWeight: '600' }}>Calories:</span> {selectedProduct.calories} kcal
                    </p>
                    <p style={{ fontSize: '14px', color: '#666666' }}>
                      <span style={{ fontWeight: '600' }}>Preparation Time:</span> {selectedProduct.preparationTime}
                    </p>
                    <p style={{ color: '#666666', marginTop: '8px' }}>{selectedProduct.description}</p>
                  </div>
                </div>
                <div>
                  <div style={{ display: 'flex', flexDirection: 'column', gap: '24px' }}>
                    {/* Default Ingredients */}
                    <div>
                      <h3 style={{ fontSize: '18px', fontWeight: '600', marginBottom: '12px' }}>
                        Included Ingredients:
                      </h3>
                      <div style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
                        {selectedProduct.ingredients
                          .filter(ingredient => ingredient.default)
                          .map((ingredient) => (
                            <div key={ingredient.name} style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                              <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
                                <Checkbox
                                  id={ingredient.name}
                                  checked={true}
                                  disabled
                                />
                                <label
                                  htmlFor={ingredient.name}
                                  style={{ fontSize: '14px', fontWeight: '500' }}
                                >
                                  {ingredient.name}
                                </label>
                              </div>
                              <span style={{ fontSize: '14px', color: '#666666' }}>Included</span>
                            </div>
                          ))}
                      </div>
                    </div>

                    {/* Optional Add-ons */}
                    <div>
                      <h3 style={{ fontSize: '18px', fontWeight: '600', marginBottom: '12px' }}>
                        Optional Add-ons:
                      </h3>
                      <div style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
                        {selectedProduct.ingredients
                          .filter(ingredient => !ingredient.default)
                          .map((ingredient) => (
                            <div key={ingredient.name} style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                              <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
                                <Checkbox
                                  id={ingredient.name}
                                  checked={selectedIngredients.has(ingredient.name)}
                                  onCheckedChange={() => handleIngredientToggle(ingredient.name, false)}
                                />
                                <label
                                  htmlFor={ingredient.name}
                                  style={{ fontSize: '14px', fontWeight: '500' }}
                                >
                                  {ingredient.name}
                                </label>
                              </div>
                              <span style={{ fontSize: '14px', color: '#666666' }}>
                                +${ingredient.price.toFixed(2)}
                              </span>
                            </div>
                          ))}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: '24px', paddingTop: '24px', borderTop: '1px solid #e5e7eb' }}>
                <span style={{ fontSize: '24px', fontWeight: 'bold', color: '#FFB800' }}>
                  ${calculateTotalPrice(selectedProduct, selectedIngredients).toFixed(2)}
                </span>
                <button
                  style={{
                    backgroundColor: '#FFB800',
                    color: '#ffffff',
                    padding: '8px 24px',
                    borderRadius: '8px',
                    border: 'none',
                    cursor: 'pointer',
                    fontSize: '14px',
                    fontWeight: '500',
                    transition: 'opacity 0.2s',
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.opacity = '0.9';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.opacity = '1';
                  }}
                >
                  Add to Cart
                </button>
              </div>
            </div>
          </DialogContent>
        )}
      </Dialog>
    </div>
  );
};

export default Restaurant;